@import "assets/scss/common.scss";


.basicInfo {
    padding: 5px;
    .contentPart {
        padding: 10px;
        margin: auto;
        height: 65vh;
        overflow-y: auto;
        overflow-x: hidden;
        width: 100%;
    }

    .line {
        height: 40px;
    /* width: 1px; */
    border: 1px solid #A5A8A9;
    }

    .btn {
        column-gap: 17px;
    }

    .rowWidth {
        width: 270px;
    }

    .AddressWidth {
        width: 540px;
    }

    .btnWidth {
        width: 160px;
    }

    .propertyLabel {
        color: #090909;
    }
    .basicForm {
        background: #FAFBFB;
        border-radius: 16px;
        margin: 15px;
        padding: 10px;
    }
    .basicForm2 {
        // background: #FAFAFA;
        border-radius: 16px;
    }
    .gap {
        gap: 10px;
    }
    .tabwrap {
        max-width: 368px;
        margin-top: 24px;
        // background: #ffffff;
        
    }
    .room_package_tab {
        display: flex;
        padding: 4px 6px;
        background-color: #ffffff;

    
      }
      .title {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        font-family: "Inter";
        color: #090909;
        margin-bottom: 16px;
      }
      .customfile {
          border: 1.5px solid #e8e9e9;
          border-radius: 16px;
          .label {
            // height: 90px;
            width: 100%;
            padding: 0 15px;
            display: flex;
            gap: 17px;
            position: relative;
            cursor: pointer;
            .uploadtext {
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
              font-family: "Barlow";
              color: #4b5153;
            }
            .uploadbtn {
              font-size: 14px !important;
              font-weight: 500 !important;
              line-height: 20px;
              font-family: "Barlow";
              padding: 11px 26px;

              position: absolute;
              z-index: 2 !important;
              right: 24px;
              top: 0;
              bottom: 0;
              margin: auto;
              border-radius: 10px;
              color: #935ed5;
              background-color: #fff;
              display: inline-table;
              text-decoration: none;
            }
            .format {
              font-size: 12px;
              font-weight: 500;
              line-height: 18px;
              font-family: "Barlow";
              .mb {
                font-size: 12px;
                font-weight: 600;
                line-height: 18px;
                font-family: "Barlow";
              }
            }
          }
        }
        .customfile2 {
            border: 1.5px solid #e8e9e9;
            border-radius: 16px;
            margin: 16px;
        }

        .addServiceWrap {
            padding-left: 19px;
            text-decoration: none;
            cursor: pointer;
        
            .iconbtn {
              padding: 0;
              margin-right: 8px;
              width: 18px;
              height: 18px;
              color: #935ed6;
        
              .circleAdd {
                width: 18px;
                height: 18px;
                color: #935ed6;
              }
            }
        
            .addServiceText {
              @extend .B3-Medium;
              color: #935ed6;
            }
          }

          hr {
            border-top: 2px solid #e8e9e9;
          }


          .overlay {
            // display: flex;
            position: absolute;
            top: 9em;
            left: 0;
            padding-right: 102px;
            justify-content: end;
            width: 100%;
            height: 100%;
            /* background: rgba(0, 0, 0, 0.5); */
            z-index: 2;
            display: flex;
            align-items: center;
            justify-content: end;
          }
        
          /* Style for the filters frame */
          .filtersFrame {
            width: 408px;
            height: 268px;
            background: var(--primary-09, #f6f6f6);
            /* Background color of the frame */
        
            z-index: 3;
            /* Higher z-index to display above the overlay */
            /* Add other styling for your frame as needed */
            border-radius: 16px;
            border: 1px solid var(--primary-09, #f6f6f6);
        
            /* Dropdown Shadow */
            box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
          }


          .propertyLabel {
            color: #090909;
          }
          

  .dropdownCTA {
    align-items: flex-start;
    display: flex;
    gap: 16px;
    position: relative;
    //   width: 170px;
  }

  .today {
    color: #4b5153;
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;

    position: relative;
    white-space: nowrap;
    width: fit-content;
    padding: 15px 7px 0px 12px;
  }

  .statusDropdown {
    position: absolute;
    top: 47px;
    left: 0;
    width: 60%;
    max-height: 200px;
    overflow-y: auto;
    background: #fafbfb;
    border: 1px solid #d2d3d4;
    z-index: 3;
    display: flex;
    flex-direction: column;
    // align-items: center;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 4px 4px 20px 0px rgba(214, 214, 214, 0.8);
  }

  .div5 {
    align-items: center;
    display: inline-flex;
    border-radius: 10px;
    border: 1px solid var(--primary-07, #d2d3d4);
    background: #fff;
    gap: 16em;
  }
  .textwrapper {
    font-family: Barlow;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    color: #a5a8a9;
  }


    }